@import url("https://cdnjs.cloudflare.com/ajax/libs/weather-icons/2.0.12/css/weather-icons.min.css");
@import url("https://fonts.googleapis.com/css?family=Poiret+One");


*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

:root{
     --rad:0.7rem;
     --dur:0.3s;
     --color-dark:#2f2f2f;
     --color-light:#fff;
     --color-brand:#57bd84;
     --font-fam:"Lato",san-serif;
     --height:5rem;
     --btn-width:6rem;
     --bez:cubic-bezier(0,0,0.43,1.49);
}

body{
    background-color: #f5f5f5;
    background:var(--color-dark);
    height: 100vh;
    min-height: 100vh;
    display: grid;
    place-items: center;
}

.widget{
    display: flex;
    flex-wrap: wrap;
    height: 300px;
    width: 350px;
    background-color: var(--color-light);
    cursor: pointer;
    border-radius: 20px;
    box-shadow: 0 27px 55px 0 rgba(0,0,0,0.3),
    0 17px 17px 0 rgba(0,0,0,0.15);
}

.widget .weatherIcon{
    height:50%;
    background: #fafafa;
    color: var(--color-dark);
    display: flex;
    align-items: center;
    justify-content: space-around;
    font-size: 100px;
    flex:1 100%;
    border-top-left-radius: 20px;
    border-top-right-radius: 20px;
}

.widget .weatherInfo{
    /* margin-top: px; */
    flex: 0 0 70%;
    height: 40%;
    display: flex;
    align-items:center;
    color: white;
    background: #080705;
}

.widget .weatherInfo .temperature{
    flex: 0 0 40%;
    display: flex;
    width: 100%;
    font-size:38px;
    justify-content: center;
    margin-left: 10px;
    margin-right: 15px;
   
}

.widget .weatherInfo .description{
    flex: 0 50%;
    display: flex;
    flex-direction: column;
    width: 100%;
    height: 100%;
    justify-content: center;   
}

.widget .weatherInfo .description .weatherCondition{
    text-transform: uppercase;
    font-size: 28px;
    font-weight: 400;
    margin-right: 10px;
    
}

.widget .Date{
    /* margin-top: -15px; */
    flex: 0 0 30%;
    height: 40%;
    background: #70c1b3;
    display: flex;
    justify-content:space-around;
    align-items: center;
    color:rgb(38, 38, 38);
    font-size: 18px;
    font-weight: 800;
    text-align: center;

    
}

.widget .extra-temp{
     background: #fafafa; 
    flex: 1 100%;
    padding: 5% 1%;
    border-bottom-left-radius: 20px;
    border-bottom-right-radius: 20px;
    display: flex;
    justify-content: space-around;
    align-items: center;
    /* font-size: 100px; */
}

.temp-info-minmax,
.weather-extra-info,
.two-sided-section{
    display: grid;
    grid-template-columns: repeat(2,1fr);
    text-align: center;
    align-items: center;
    color: #70c1b3;
}

.widget .extra-temp .extra-info-leftside{
    /* text-align: left; */
    font-weight: 600;
    text-transform: capitalize;
    color: #080705;
    font-size: 10px;
}

.widget .extra-temp p{
    font-size: 22px;
    font-weight: 800;
}


.Heading{
    text-align: center;
    background-color: brown;
    font-size: 30px;
    color: #fff;
    font-family: "Lato",sans-serif;
    padding:10px;
}

.wrap{
    display: flex;
    justify-content: center;
    text-align: center;
    margin-top: 50px;
    margin-bottom: 50px;
}

.search{
    width: 100%;
    position: relative;
    display: flex;
    justify-content: center;
}

.searchTerm{
    width: 70%;
    border: 1px solid black;
    padding: 16px;
    height: 20px;
    border-radius: 5px 0px 0px 5px;
    outline: none;
    font-size: 18px;
}

.searchTerm:focus{
    color: #00b4cc;
}

.searchButton{
    width: 80px;
    height: 34px;
    border: none;
    border-radius: 5px 5px 5px 5px;
    text-align: center;
    cursor: pointer;
    color: #fff;
    outline: none;
    font-size: 16px;
    background:#048b9c;
    margin-left: 5px;
}

.searchButton:hover{
    background-color:#17b8ce;
}